<template>
<!--      <div :class="필터 + ' filter-item'" :style="`background-image : url(${uploadImg})`"></div> -->
    <div @click="fire" :class="필터" class="filter-item" :style="`background-image : url(${uploadImg})`">
    <slot></slot>
    <!-- <button @click="fire">버튼</button> -->
    </div>
</template>

<script>
export default {
    name: 'filterbox',
    props: {
        uploadImg : String,
        필터 : String,
    },
    methods: {
        fire() {
            // 다른 컴포넌트로 이벤트 발사
            // this.emitter.emit('작명', '데이터')
            this.emitter.emit('박스클릭함', this.필터)
        }
    }
}
</script>

<style>
.filter-item {
    width: 100px;
    height: 100px;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color : white;
    background-size: cover;
    background-position : center;
}
</style>