<!-- 오늘의 숙제 : 

인스타그램 사진을 누르면 좋아요 갯수가 +1 되게 만들어보십시오.

그런데 사진을 다시 누르면 좋아요 갯수가 -1 되게 만들어야합니다. 

이거 하려면 일단 좋아요 갯수를 store.js에 저장한 후 Post.vue에 꽂아넣어야겠죠?

그 다음에 좋아요/좋아요 취소 기능을 만들어보십시오.

사진 3개 다 작동하도록 만들어봅시다. -->

<!-- 
  앱뷰에 있는 데이터 스토어로 옮겨보는건 어떨까요?
  앱뷰에서만 쓸건 안옮겨도 되고 다른 컴포넌트에도 사용되는 데이터들.
 -->

<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <a href="App.vue">
    <img src="./assets/logo.png" class="logo" />
    </a>
  </div>

  <!-- sore.js에 저장된 데이터 꺼내쓰기/변경하기 -->
  <h4>안녕 {{ $store.state.age }}세의 {{ $store.state.name }}</h4>
  <!-- 이렇게 컴포넌트에서 데이터 직접 바꾸면 안됨. Vuex 국룰 -->
  <!-- <button @click="$store.state.name = '박'">성씨바꿔주세요</button> -->
  <!-- sore.js에 수정해달라고 요청 -->
  <button @click="$store.commit('이름변경')">이름변경</button>
  <button @click="$store.commit('나이증가', 10)">나이맥이기</button>

  <!-- actions 예제 -->
  <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">더보기버튼</button>

  <!-- computed 예제 -->
  <!-- methodes -->
  <p>{{ now() }} {{ 카운터 }}</p>
  <button @click="카운터++">버튼</button>
  <!-- couputed : coputed 함수는 이름만쓰셈(계산결과 저장하는 곳이라서) -->
  <p>{{ now2 }} {{ 카운터 }}</p>
  <button @click="카운터++">버튼</button>

  <!-- mapState 예제 -->
  <p>{{ name }}</p>

  <!-- mapMutations 예제 -->
  <button @click="이름변경()">이름변경</button>



  <Container @write="작성한글 = $event" :vuestaData="vuestaData" :step="step" :uploadImg="uploadImg"/>



  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>

      <!-- accept="image/*" : 업로드할 파일 선택할 때 이미지만 보여지도록 선택되어져 있게 해줌. 그러나 모든 파일 선택하면 모든 확장자 파일 다 나옴 -->
    </ul>
  </div>

  <!-- 탭기능 예제 -->
<!--   <div v-if="step == 0">내용0</div>
  <div v-if="step == 1">내용1</div>
  <div v-if="step == 2">내용2</div>
  <button @click="step = 0">버튼0</button>
  <button @click="step = 1">버튼1</button>
  <button @click="step = 2">버튼2</button> -->

</template>

<script>
import Container from './components/Container.vue';
import vuestaData from './assets/vuestaData.js';
import axios from 'axios' // ajax 요청 하고싶은 곳에 import하고 요청하면 됨
import {mapMutations, mapState} from 'vuex';

export default {
  name: 'App',
  data() {
    return {
      step : 0, // 현재 페이지
      vuestaData : vuestaData,
      더보기 : 0,
      uploadImg : '',
      선택한필터 : '',
      카운터 : 0,
    }
  },
  components: {
    // Container : Container,
    Container,

  },
  // 계산결과를 저장하는곳
  // 컴포넌트로드시 한 번 실행되고 그 값을 계쏙 저장해서 씀(함수를 불러도 실행안됨)
  // 꼭 return을 사용해야함(계산 결과 저장하는 곳이라 계산결과를 퉤 뱉어준다고 써야함)
  computed: {
    // mapState
    name() {
      return this.$store.state.name
      // 이제 name이라고만 써서 꺼내쓸 수 있음
    },
    ...mapState(['name', 'age', 'likes']),
    // 이제 $store.state. 없이 자료이름만 써서 꺼내쓸 수 있음
    ...mapState({ 내이름 : 'name' }),
    // ...mapState({ 작명 : '자료' }) : {{ 내이름 }} 이렇게 갖다쓸 수 있음

    now2() {
      return new Date() // 현재 시간을 출력해줌
    },
  },
  // 함수를 부를 때마다 안의 코드가 실행됨
  methods: {
    ...mapMutations (['setMore', '좋아요', '이름변경']),
    // 이제 $store.commit. 없이 @click="증가(10)" 이렇게 쓸 수 있음
    now() {
      return new Date() // 현재 시간을 출력해줌
    },
    publish() {
      var 새게시물 = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.uploadImg,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: this.선택한필터
      };
      this.vuestaData.unshift(새게시물); // vuestaData 맨 앞에 내가쓴거 추가
      this.step = 0;
    },
    upload(e) {
      let 파일 = e.target.files; // 내가 업로드한 파일들 담겨있어요 (fileList에 담아줌)
      console.log(파일[0]);
      let imgUrl = URL.createObjectURL(파일[0]);
      console.log(imgUrl);
      this.uploadImg = imgUrl;

      this.step = 1; // 사진 꾸미는 페이지로 넘어가주세요
    },
    more() {
      // axios 사용법 몇개 더 알려주자면
      // 내가 원하는 url로 post 요청할 수 있음 (데이터 전송)
      // axios.post('URL', {name : 'kim'})
      // .then((result)=> {
      //    요청 성공시 싱행할 코드~
      // })
      // .catch((err)=> {
      //    요청 실패시 실행할 코드~  
      // })

      // axios.get('url') : get요청

      axios.get(`https://codingapple1.github.io/vue/more${this.더보기}.json`)
      .then((결과)=> { // 결과(result) : get 요청으로 가져온 data
        // 요청성공시 실행할 코드~
        console.log(결과.data);
        this.vuestaData.push(결과.data); // array 마지막 항목 뒤에 추가
        this.더보기++;
      })
      // 더보기버튼 누르면 서버에서 추가 게시물 가져옴
    }
  },
  mounted() {
    // emit으로 발사한 이벤트 받는곳
    // 작명 : 누가 이런 이벤트 발사하면
    this.emitter.on('박스클릭함', (a)=>{ // a: 이벤트 발사할때 들어있던 데이터
      // 이 코드 실행해주셈
      this.선택한필터 = a
    })
  }

}
</script>

