import { createApp } from 'vue'
import App from './App.vue'
import mitt from 'mitt' // 라이브러리 등록. '/경로' 처럼 슬래쉬가 없으면 라이브러리 문법
let emitter = mitt(); // emitter라는 변수 하나 만듬
let app = createApp(App)
app.config.globalProperties.emitter = emitter;

// app.config.globalProperties : 글로벌한 변수보관함(모든 컴포넌트에서 갖다쓸 수 있는 변수같은거 보관하는)에 emitter라는 항목 하나 추가해주세요
// emitter : mitt라이브러리에서 제공하는 함수

// 자주 쓰는 라이브러리 그런거 있으면 여기다가 등록하셈
// ex) app.config.globalProperties.axios = axios;
// 이렇게 등록해놓으면 vue파일에서 import axios 해올 필요 없이 this.axios로 사용가능

import store from './store.js'

import './registerServiceWorker'

app.use(store).mount('#app')
