<template>
  <div v-if="step == 0">
    <Post v-for="(a, i) in vuestaData" :key="i" :vuestaData="vuestaData[i]">
    <!-- <slot> 여러개 데이터 바인딩하기 -->
      <template v-slot:likes>{{ $store.state.likes[i] }}</template>
      <template v-slot:name>{{ vuestaData[i].name }}</template>
      <template v-slot:content>{{ vuestaData[i].content }}</template>
      <template v-slot:date>{{ vuestaData[i].date }}</template>
    </Post>
  <button @click="more">더보기</button>
  </div>

  <!-- 필터선택페이지 -->
  <div v-if="step == 1">
    <div :class="선택한필터" class="upload-image" :style="`background-image : url(${uploadImg})`"></div>
    <div class="filters">
      <FilterBox v-for="필터 in 필터들" :key="필터" :uploadImg="uploadImg" :필터="필터">
      <!-- 자식컴포넌트 <slot>에 전달할 데이터 -->
      {{ 필터 }}
      </FilterBox>
    </div>
  </div>

  <!-- 글작성페이지 -->
  <div v-if="step == 2">
    <div :class="선택한필터" class="upload-image" :style="`background-image : url(${uploadImg})`"></div>
    <div class="write">
      <textarea placeholder="게시글을 작성해주세요" @input="$emit('write', $event.target.value)" class="write-box"></textarea>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";

export default {
  name: "container",
  components: {
    Post,
    FilterBox,
  },
  props: {
    vuestaData: Array,
    step: Number,
    uploadImg: String,
  },
  data() {
    return {
      필터들 : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
"inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
"reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
        선택한필터 : '',
      }
    },
  mounted() {
    // emit으로 발사한 이벤트 받는곳
    // 작명 : 누가 이런 이벤트 발사하면
    this.emitter.on('박스클릭함', (a)=>{ // a: 이벤트 발사할때 들어있던 데이터
      // 이 코드 실행해주셈
      this.선택한필터 = a
    })
  }
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>
