import { createStore } from 'vuex'
import axios from 'axios'

const store = createStore({
  state(){
    return {
      // state 보관하고 싶으면 여기하셈
      name : 'kim',
      age : 20,
      likes : [35, 24, 86],
      좋아요눌렀니 : [false, false, false],
      more : {},
    }
  },
  // state 변경하는 방법 정의 하는곳
  mutations: {
    이름변경(state) {
        state.name = 'park'
    },
    나이증가(state, payload) { // payload(화물) : 부탁할때 첨부한 데이터가 들어있음
        state.age += payload
    },
    좋아요(state) {
      for (let i = 0; i < state.likes.length; i++) {
        if (state.좋아요눌렀니[i] == false) {
          state.likes[i]++;
          state.좋아요눌렀니[i] = true;
        } else {
          state.likes[i]--;
          state.좋아요눌렀니[i] = false;
        }
      }
    },
    setMore(state, data) {
      state.more = data
    }
    
  },
  // Vuex에서 ajax 요청하는 곳, 오래걸리는 작업
  actions: {
    getData(context) {
      axios.get('https://codingapple1.github.io/vue/more0.json')
      .then((a)=>{
        console.log(a.data);
        context.commit('setMore', a.data)
      })
    },
  },
})

export default store;